// Lesson- Chapter - Unit
import {createSlice, createAsyncThunk, current} from '@reduxjs/toolkit';
import {axios} from 'axios';


const MyBookData = [ //
    
    {grade : "E2", semester: 1, name : "", period_s : "2022.05.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book9"}
    , {grade : "E2", semester: 1, name : "", period_s : "2022.03.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book8"}
    , {grade : "E2", semester: 2, name : "", period_s : "2022.05.01", period_f : "2023.02.28", bookcode: "E", peroid : true, "id":"book7"}
    , {grade : "E1", semester: 2, name : "", period_s : "2022.05.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book6"}
    , {grade : "E2", semester: 2, name : "", period_s : "2022.05.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book5"}
    
]


const BookContents  = [ //, 
    null,
    null,
    [
        {".   ": [
            {title : " ", lectureState:100, study: "10/12", complete : true },
            {title : " ", lectureState:45, study: "12/12", complete : true},
            {title : "  ", lectureState:45, study: "10/12", complete : true},
            {title : "  ", lectureState:45, study: "-/12", complete : false},
            {title : "   ", lectureState:45, study: "5/12", complete : false},
            {title : " ", study: "-/30", complete : false}
        ]},
        {". ": [
            {title : " ", lectureState:0, study: "-/12", complete : false},
            {title : " (1)", lectureState:0, study: "-/12", complete : false},
            {title : " (2)", lectureState:0, study: "-/12", complete : false},            
            {title : " ", study: "-/30"}
        ]}]
]

const changeBookID = createAsyncThunk(

    'changeBookID', (id) =>{
       
        console.log(id)  ;
        return id;       
    }
  )


const bookSlice = createSlice({

    name : "BOOK",
    initialState : {
        bookID : null,
        lesson : null,
        unit : null,       
        bookData :MyBookData,
        bookContents : BookContents
    },
    reducers : {
        
        setBookID : (state,action) =>{
            
            state.bookID = action.payload
        },

        setLesson : (state,action) => {
            state.lesson = action.payload
        },

        setUnit : (state, action) =>{
            state.unit = action.payload
        }
    },

    extraReducers : {

       [changeBookID.pending] : (state,action) => {
            
            current(state).bookID = action
             
       }       
    }
})

export {changeBookID}
export const {setBookID, setLesson, setUnit} = bookSlice.actions;
export default bookSlice.reducer;
