import { useState, useEffect } from "react";

const Lnb_S = () => {      
    
    const [showLnb,displayLnb] = useState(false);

    useEffect(()=>{
        document.addEventListener('click', (e) => {            
            if(e.target.className!=="lnb-btn") displayLnb(false)
         })
    },[]);
    
    return (
        <>
        <button className="lnb-btn" onClick={()=>{displayLnb(true)}}>=</button>
        <div className={"lnb-sub " + (showLnb? "active" : "" )}>
            <ul>
                <li><a href="#"></a></li>
                <li><a href="#">  </a></li>
                <li><a href="#"> </a></li>
                <li><a href="#"> </a></li>
                <li><a href="#"> </a></li>
            </ul>
        </div>
        </>
    )
}

export default Lnb_S;